





































































































import Logo from "../misc/Logo.vue";
import NumberCard from "../home/NumberCard.vue";
import JobExecutionCalendar from "@/components/misc/JobExecutionCalendar.vue";
import { Vue, Component, Prop, Mixins, Watch } from "vue-property-decorator";
import { jobsModule, routerModule, authModule } from "@/store/modules";
import DoesNotExistScreen from "@/components/misc/DoesNotExistScreen.vue";
import { EmailNotification, IGetBackupJobStatsResponse } from "@/shared";
import {
  humanReadableExpression,
  datesInDistanceInWords,
  nextCronExecutionDates,
  relativeDate
} from "@/misc/cron";
import { getJobStats } from "@/store/api";

@Component({
  components: {
    NumberCard,
    DoesNotExistScreen,
    JobExecutionCalendar
  }
})
export default class RepositoryOverviewComponent extends Mixins() {
  get jobId(): string {
    return routerModule.params.jobId;
  }

  get jobData() {
    return jobsModule.getById(this.jobId);
  }

  get details() {
    if (!this.jobData) return [];
    return [
      { title: this.$t("job.details.name"), value: this.jobData.name },
      {
        title: this.$t("job.details.max-number-of-backups"),
        value: this.jobData.maxBackups
      },
      {
        title: this.$t("job.details.email-notification"),
        value: (
          [
            {
              text: this.$t("notification-settings.never"),
              value: EmailNotification.never
            },
            {
              text: this.$t("notification-settings.on-error"),
              value: EmailNotification.onerror
            },
            {
              text: this.$t("notification-settings.always"),
              value: EmailNotification.always
            }
          ].find(
            i => !!(this.jobData && i.value === this.jobData.emailNotification)
          ) || { text: this.$t("notification-settings.invalid") }
        ).text
      },
      {
        title: this.$t("job.details.repeat-interval"),
        value:
          humanReadableExpression(this.jobData.repeatPattern) ||
          this.$t("scheduling.cron.invalid-expression")
      },
      {
        title: this.$t("job.details.next-executions"),
        value: datesInDistanceInWords(
          nextCronExecutionDates(
            this.jobData.repeatPattern,
            this.jobData.startDate,
            3
          )
        ).join(", ")
      }
    ];
  }

  stats: IGetBackupJobStatsResponse | null = null;

  @Watch("jobId", { immediate: true })
  async getStats() {
    const response = await getJobStats(authModule.userId, Number(this.jobId));
    if (response.payload) {
      this.stats = response.payload;
    }
  }

  get lastExecutionTime() {
    if (
      this.stats &&
      this.stats.lastExecutions &&
      this.stats.lastExecutions.length > 0
    ) {
      return this.stats.lastExecutions[0].date;
    }
    return null;
  }

  get lastExecutionRelative() {
    if (!this.lastExecutionTime) {
      return null;
    }
    return relativeDate(new Date(this.lastExecutionTime));
  }
}
